import { useMemo } from 'react';

export const useGetErrorMessage = () => {
  const getErrorMessage = useMemo(() => (error, verbForErrorMessage, objectForErrorMessage) => {
    let errorMessage = `${verbForErrorMessage}${objectForErrorMessage}`;
    if (error instanceof ReferenceError) {
      errorMessage = `${verbForErrorMessage}${objectForErrorMessage}`;
    } else if (error.response) {
      const status = error.response.status;
      switch (status) {
        case 404:
          errorMessage = `${verbForErrorMessage}${objectForErrorMessage}`;
          break;
        case 403:
          errorMessage = `${verbForErrorMessage}${objectForErrorMessage}`;
          break;
        case 400:
          errorMessage = `${verbForErrorMessage}${objectForErrorMessage}`;
          break;
        default:
          errorMessage = `${verbForErrorMessage}${objectForErrorMessage}`;
          break;
      };
    } else if (error.request) {
      errorMessage = `${verbForErrorMessage}${objectForErrorMessage}`;
    } else {
      errorMessage = `${verbForErrorMessage}${objectForErrorMessage}`;
    };
    window.alert(errorMessage);
  }, []);

  return { getErrorMessage };
};